<template>
  <div class="web-client">
    
    <a-form class="form" :form="form">
      <a-form-item :key="index" v-for="(item, index) in data" :label="($t('sparm.' + item.parentCode +'_'+ item.code) != 'sparm.' + item.parentCode +'_'+ item.code)?$t('sparm.' + item.parentCode +'_'+ item.code):item.description" v-bind="formItemLayout">
        <a-input
          v-if="item.v2 != 'HEAD_ICON'"
          v-decorator="[
            item.code,
            {initialValue: item ? item.v1 : ''}
          ]" 
        ></a-input>
        <a-upload
          v-if="item.v2 == 'HEAD_ICON'"
          name="avatar"
          style="width:102px; float: left"
          list-type="picture-card"
          class="avatar-uploader"
          :show-upload-list="false"
          :before-upload="beforeUpload"
        >  
          <div class="bg" :style="'background:'+ colorList[key]">
            <img :src="imageUrl" alt="avatar" class="avatar"/>
          </div>
        </a-upload>
        <div class="color-choose" v-if="item.v2 == 'HEAD_ICON'">
          <div class="color">
            <div :key="idx" v-for="(i, idx) in colorList" class="color-item">
              <div :style="'background:'+ i" @click="onChange(idx)" :class="key == idx ? 'active' : ''"></div>
            </div>
          </div>
          <span class="tips">{{ $t('param.tip') }}</span>
        </div>
      </a-form-item>
    </a-form>
    <a-button @click="onSubmit" type="primary" class="sub_btn">{{ $t('param.btn_ok') }}</a-button>
  </div>
</template>
<script>
  import { axios } from '@/utils/request'
  export default {
    name: 'WebClient',
    data() {
      return {
        form: this.$form.createForm(this),
        data: {},
        key: '0',
        colorList: ['#fff','#ccc', '#666', '#333', '#000', '#001efb', '#fffe34','#ff0f00', '#0dfd36'],
        basePath: process.env.NODE_ENV === 'production'? '/inhe-platform-mdm/': '/',
        imageUrl: '',
        formItemLayout: {
          labelCol: {
            span: 4,
          },
          wrapperCol: {
            span: 20
          },
        },
      }
    },
    mounted(){
      this.formData = new FormData()
      this.$emit('done')
    },
    methods: {
      initData(record){
        const that = this
        that.formData.set('parentCode', record.code)
        const param = { parentCode: record.code}
        axios({
          url: 'sys/parameter/search-type-detail',
          method: 'get',
          params: param
        }).then(response=>{
          that.data = response.result
          for(let i=0;i<that.data.length;i++){
            if(that.data[i].v2 == 'HEAD_ICON'){
              const path = JSON.parse(that.data[i].v1)
              that.imageUrl = that.basePath + 'api/auth/node-icon/' + path.bucket + '/' + path.file
            }
          }
        })
      },
      beforeUpload(file) {
        const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png'
        if (!isJpgOrPng) {
          this.$message.error(this.$t('param.tips1'))
          return false
        }
        const isLt2M = file.size / 1024 / 1024 < 2
        if (!isLt2M) {
          this.$message.error(this.$t('param.tips2'))
          return false
        }
        this.formData.set('fileName', file)
        this.getBase64(file, imageUrl => {
          this.imageUrl = imageUrl
        })
        return false
      },
      getBase64(img, callback) {
        const reader = new FileReader()
        reader.addEventListener('load', () => callback(reader.result))
        reader.readAsDataURL(img)
      },
      onSubmit() {
        const that = this
        this.form.validateFields((err,values) => {
          if (!err) {
            that.formData.set('items', JSON.stringify(values))
            axios({
              url: '/sys/parameter/update',
              method: 'post',
              headers: {
                'Content-Type': 'multipart/form-data'
              },
              data: that.formData
            })
            .then(result=>{
              that.loading = false
              if(result.result == true){
                this.$emit('ok')
              }
              this.visible = false
            })
          }
        })
      },
      onChange(index) {
        this.key = index
      }
    }
  }
</script>
